import Vue from 'vue';
import VueRouter from 'vue-router';

Vue.use(VueRouter);

const routes = [
	{
		path: '/',
		redirect: '/home'
	},
	{
		path: '/home',
		name: 'HomePage',
		component: () => import(/* webpackChunkName: "poetry" */ '../views/HomePage.vue'),
	},
	{
		path: '/detail',
		name: 'Detail',
		component: () => import(/* webpackChunkName: "poetry" */ '../views/Detail.vue'),
		meta: {
			hideNavbar: true
		}
	},
	{
		path: '/category',
		name: 'Category',
		component: () => import(/* webpackChunkName: "poetry" */ '../views/Category.vue'),
		meta: {title: '分类'},
		children: [
			{
				path: '/category/list', name: 'CategoryItemList',
				component: () => import(/* webpackChunkName: "poetry" */ '../views/CategoryItemList.vue'),
			}
		]
	},
];

const router = new VueRouter({
	linkActiveClass: 'active',
	routes
})

export default router;
