<template>
    <div class="navbar_wrap">
        <router-link v-for="item in navArray" :key="item.path" :to="item.path">
            <i v-if="item.icon" :class="item.icon"></i>
            <span>{{item.text}}</span>
        </router-link>
    </div>
</template>

<script>
    import {reactive, toRefs} from '@vue/composition-api';

	export default {
		name: 'Navbar',
        setup(){
			const state = reactive({
                navArray: [
                    {path: '/home', text: '首页', icon: 'el-icon-s-home'},
                    {path: '/category', text: '分类', icon: 'el-icon-s-management'},
                ]
            });

			return {
                ...toRefs(state)
            }
        }
	}
</script>

<style scoped>
    .navbar_wrap {
        position: fixed;
        bottom: 0;
        left: 0;
        width: 100.5%;
        display: flex;
        justify-content: space-around;
        align-items: center;
        font-size: 0.24rem;
        height: 1rem;
        background: url('../assets/images/message_bg.png') repeat center center;
        z-index: 5;
    }

    .navbar_wrap a {
        display: flex;
        justify-content: center;
        flex-direction: column;
        align-items: center;
        text-decoration: none;
        width: 25%;
        color: #666;
    }

    .navbar_wrap a.active {
        color: #38f;
    }

    .navbar_wrap i {
        font-size: 0.42rem;
    }
</style>